import "./story.css";
import "../../config/common.css"
import CameraControls from 'camera-controls';
import * as THREE from 'three';
import {distance} from "three/examples/jsm/nodes/ShaderNode";

const Typewriter = require("typewriter-effect/dist/core");
const language = navigator.language || navigator.userLanguage;
const pageNames = require("../../config/pageNames.json")

//todo
const italianText = `
`
//todo
const englishText = "todo"

window.onload = () => {
  const library = document.getElementById("library");
  const links = document.getElementsByClassName("diagonal")
  Array.from(links).forEach(link => {
    link.innerText = pageNames[link.innerText][language === "it-IT"? "it" : "en"]
  })

  const it = "La mia storia";
  const en = "My story";
  const speed = 25;
  const pause = 1500;

  new Typewriter('#splash', {
    strings: [language === "it-IT"? it : en],
    autoStart: true,
    cursor: "_",
    delay: speed,
    deleteSpeed: speed,
    pauseFor: pause
  });

  setTimeout(() => {
    document.getElementById("splash").remove();
    startStory();
  }, 1/*(language === "it-IT"? it : en).length * 4 * speed + pause*/)

  const maintext = document.getElementById("maintext");

  maintext.innerText = language === "it-IT" ? italianText : englishText;
}

function startStory() {
  CameraControls.install( { THREE: THREE } );

  const width  = window.innerWidth;
  const height = window.innerHeight;
  const clock = new THREE.Clock();
  const scene = new THREE.Scene();

  const renderer = new THREE.WebGLRenderer();
  renderer.setSize(width, height);
  document.body.appendChild( renderer.domElement );

  const camera = new THREE.PerspectiveCamera( 50, width / height, 0.01, 100 );

  // This is required otherwise camera-controls don't work
  camera.position.set(0, 0, 20);

  const cameraControls = new CameraControls( camera, renderer.domElement );

  const loader = new THREE.TextureLoader();
  const geometry = new THREE.IcosahedronGeometry(10, 16);
  const material = new THREE.MeshBasicMaterial({
    map: loader.load("../../assets/4k.jpg")
  })
  const earthMesh = new THREE.Mesh(geometry, material)
  scene.add(earthMesh)

  function render() {
    requestAnimationFrame(render);

    const delta = clock.getDelta();
    const elapsed = clock.getElapsedTime();
    const updated = cameraControls.update(delta);

    renderer.render(scene, camera);
  }
  render();
}
